import { Col, Row, Spin } from 'antd';
import React, { useEffect, useState } from 'react';
//animation
import { motion } from 'framer-motion';
import InfoCard from '../UiComponents/Others/InfoCard';
import { Helmet } from 'react-helmet';
import axios from 'axios';
import { Link } from 'react-router-dom';

const AboutAlumni = () => {
  const [data, setdata] = useState();
  const [spin, setspin] = useState(false);
  const [alumnibatch, setalumniBatch] = useState();
  let alumniBatches = [];

  useEffect(() => {
    setspin(true);
    axios
      .get(`https://ginnovation-server.herokuapp.com/api/alumni`)
      .then((resp) => {
        setspin(false);
        console.log(resp.data);
        setdata(resp.data);
        resp.data &&
          resp.data.map((value, index) => {
            alumniBatches.push(value.batch);
          });
        const uniqueBatches = [...new Set(alumniBatches)];
        setalumniBatch(uniqueBatches);
      })
      .catch((err) => {
        setspin(false);
        console.log(err);
      });
  }, []);

  return (
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.5 }}
      className='about-common-page about-alumni'
    >
      <Helmet>
        <title>Alumni of Ginnovation Club</title>
        <meta
          name='description'
          content="Creating an engaged, supportive alumni network is crucial to an institution's sucess. Ginnovation is proud of it's alumni."
        />
      </Helmet>
      {spin && (
        <div className='modal-container'>
          <Spin size='large' />
        </div>
      )}
      <div className='page-top alumni-top'>
        <div className='pattern'></div>
        <header>
          <h2>About Our Alumni</h2>
          <p>Nobody is bothered about an institution more than its alumni</p>
        </header>
      </div>
      <div className='alumni container'>
        <Row gutter={8}>
          <Col md={6}></Col>
          {alumnibatch &&
            alumnibatch.map((batchValue, index) => {
              const title = `${batchValue - 4}-${batchValue}`;
              const link = `/alumni/${batchValue}`;
              return (
                <Col md={6} key={index}>
                  <motion.div
                    initial={{ y: 100, opacity: 0 }}
                    animate={{ y: 0, opacity: 1 }}
                    transition={{ duration: 0.5 }}
                  >
                    <Link to={link}>
                      <InfoCard
                        head='Batch'
                        title={title}
                        description='Card description with lots of great facts and interesting details.'
                      />
                    </Link>
                  </motion.div>
                </Col>
              );
            })}
          <Col md={6}></Col>
        </Row>
      </div>
    </motion.div>
  );
};

export default AboutAlumni;
