import React from 'react';

const InfoCard = (props) => {
  return (
    <div className='info-card'>
      <p>{props.head}</p>
      <h3>{props.title}</h3>
      <p class='small'>{props.description}</p>
      <p>View details →</p>
      <div class='go-corner'>
        <div class='go-arrow'>→</div>
      </div>
    </div>
  );
};

export default InfoCard;
